<template>
    <div>
        <div class="header">
            <div class="nav">
                <router-link to="/">
                    <img src="../assets/miaov.png" title="跳转主页面">
                </router-link>
                <router-link to="/project">
                    <i class="fa fa-home fa-fw"></i>
                    <a>我的项目</a>
                </router-link>
                <router-link to="/desk">
                    <i class="fa fa-object-group" aria-hidden="true"></i>
                    <a>工作台</a>
                </router-link>
                <router-link to="/document">
                    <i class="fa fa-book" aria-hidden="true"></i>
                    <a>我的文档</a>
                </router-link>
            </div>
            <div class="user" v-show="isLogin">
                <span><img src="../assets/portrait.png" alt=""></span>
                <strong @click="quit">退出</strong>
            </div>
        </div>
        <div class="tab">
            <p>
                <span>首页</span>/<span>{{flag}}</span>
            </p>
        </div>
        <transition name="fade" mode="out-in">
            <router-view></router-view>
        </transition>
    </div>
</template>
<script>
    let flags = {
        project:'我的项目',
        desk:'工作台',
        document:'文档'
    }
    export default {
        data(){
            return {
                isLogin:false,  //登录的时候为true,显示
                flag:''
            }
        },
        watch:{
            $route(){
                this.flag = flags[this.$route.path.slice(1)]
            }
        },
        beforeRouteEnter(to,from,next){
            next((vm)=>{
                vm.flag = flags[to.path.slice(1)]
            })
        },
        methods:{
            quit(){
                this.$local.save('miaov',null)// 清除数据
                this.$router.push('/login')
            }
        },
        created(){// 登录的时候use才显示
            let info = this.$local.fetch('miaov');
            this.isLogin = info.login
        }
    }
</script>

<style scoped>
    body{
        margin:0;
    }
    .header{
        background-color:#304057;
        padding:10px 20px;
    }
    .header::after{
        content:'';
        display:block;
        clear:both;
    }
    .header img{
        vertical-align: -10px;
        margin-right:30px;
    }
    .header a{
        /* vertical-align: middle; */
        margin-right:14px;
    }
    .header a{
        text-decoration:none;
        color:lightyellow;
        font-size:14px;
    }
    .header .nav{
        float:left;
    }
    .header .user{
        float:right;
    }
    .header .user strong{
        color:#fff;
        border:1px solid #fff;
        border-radius:20px;
        display:inline-block;
        padding:2px 5px;
        cursor:pointer;
    }
    .user img{
        width:30px;
        height:30px;
        border:1px solid #fff;
        border-radius:50%;
    }
    .tab{
        background-color:#EFF2FA;
        padding:10px 10px;
    }
    .tab p{
        margin:0;
        font-size:14px;
    }
    .tab p span:nth-of-type(2){
        color:teal;
    }
    .is-active a{
        color:lightskyblue;
    }
</style>



